body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.5)), to(rgba(0,0,0,0.5))), url("wood.jpg");
    background-image: -webkit-linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url("wood.jpg");
    background-image: -o-linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url("wood.jpg");
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url("wood.jpg");
    background-size: cover;
    background-size: 120%;
    background-position: center;
    height: 100vh;

    /* background-color: #272E4F; */
    /* background-color: #2C365D; */
    /* background-color: #606470; */
  }



